
*{
  margin:0;
  padding:0;
}
html,body{
  width:100%;
  height:100%;
}
ul{list-style:none}
a{text-decoration: none;}
img{vertical-align:top;border:none}

.clearf:after,
.clearf:before{
    content:"";
    display:block;
    clear:both;
}

.happy-scroll {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}

.happy-scroll-strip--vertical {
  position: absolute;
  right: 0px;
  top:0;
  height: 100%;
}
.happy-scroll-strip--vertical .happy-scroll-bar {
  background-color: rgba(51,51,51,0.2);
  border-radius: 7px;
  width: 6px;
  height: 0;
  -webkit-transition: width 0.4s ease, height 0.6s ease;
  transition: width 0.4s ease, height 0.6s ease;
}
.happy-scroll-strip--horizontal {
  position: absolute;
  bottom: 0px;
  left:0;
  width: 100%;
}
.happy-scroll-strip--horizontal .happy-scroll-bar {
  background-color: rgba(51,51,51,0.2);
  border-radius: 7px;
  height: 6px;
  width: 0;
  -webkit-transition: height 0.4s ease, width 0.6s ease;
  transition: height 0.4s ease, width 0.6s ease;
}
/* 在webkit内核下将滚动条隐藏，Firefox下采用容器覆盖的机制 */
.happy-scroll-container::-webkit-scrollbar  {
display: none;
}
.happy-scroll-container {
overflow: auto;
}

.happy-scroll-container .happy-scroll-content{
/* 解决横向滚动差 20px 的问题 */
display: inline-block;
}


.happy-scroll{
  width: auto!important;
  .happy-scroll-container{
    width: auto!important;
    height: 100%!important;
    .happy-scroll-content{
      width: 100%!important;
      min-height: 100%!important;
      border-right: 0!important;
      border-bottom: 0!important;
    }
  }
}












































































































































































































































































.map-echart-box{
  width: 100%;
  height: 100%;
  position: relative;
  .map-echart{
    width: 100%;
    height: 100%;
  }
  .map-echart-legend{
    position: absolute;
    bottom: 62px;
    left: 50%;
    z-index: 11;
    transform: translate(-50%);
    display: flex;
    li{
      width: 140px;
      height: 24px;
      border: 1px solid #E6E9EC;
      padding: 0 10px;
      display: inline-flex;
      align-items: center;
      font-size: 12px;
      color: #F9F9F9;
      +li{
        margin-left: 10px;
      }
    }
  }
  .perception-trend-box{
    position: absolute;
    top: 80px;
    right: 360px;
    width: 400px;
    height: 300px;
    background: rgba(255,255,255,0.90);
    border: 1px solid rgba(79,87,94,0.30);
    box-shadow: 0 19px 20px 0 rgba(32,43,53,0.20);
    border-radius: 3px;
    padding: 10px;
    box-sizing: border-box;
    z-index: 9;
    h3{
      display: flex;
      align-items: center;
      justify-content: space-between;
      .title{
        font-size: 16px;
        color: #131926;
      }
      .close{
        cursor: pointer;
      }
    }
    p{
      padding: 15px 0 40px;
      font-size: 18px;
      color: #83A7EC;
      text-align: center;
      font-weight: bold;
    }
    .perception-trend{
      width: 100%;
      height: 170px;
    }
  }
}
@media screen and (max-width: 800px) {
  .map-echart-box{
    .map-echart-legend{
      li{
        width: auto;
        padding: 0 5px;
        width: 100px;
      }
    }
    .perception-trend-box{
      right: 5px;
      top: 80px;
      width: calc(100% - 10px);
    }
  }
}
