
*{
  margin:0;
  padding:0;
}
html,body{
  width:100%;
  height:100%;
}
ul{list-style:none}
a{text-decoration: none;}
img{vertical-align:top;border:none}

.clearf:after,
.clearf:before{
    content:"";
    display:block;
    clear:both;
}

.happy-scroll {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}

.happy-scroll-strip--vertical {
  position: absolute;
  right: 0px;
  top:0;
  height: 100%;
}
.happy-scroll-strip--vertical .happy-scroll-bar {
  background-color: rgba(51,51,51,0.2);
  border-radius: 7px;
  width: 6px;
  height: 0;
  -webkit-transition: width 0.4s ease, height 0.6s ease;
  transition: width 0.4s ease, height 0.6s ease;
}
.happy-scroll-strip--horizontal {
  position: absolute;
  bottom: 0px;
  left:0;
  width: 100%;
}
.happy-scroll-strip--horizontal .happy-scroll-bar {
  background-color: rgba(51,51,51,0.2);
  border-radius: 7px;
  height: 6px;
  width: 0;
  -webkit-transition: height 0.4s ease, width 0.6s ease;
  transition: height 0.4s ease, width 0.6s ease;
}
/* 在webkit内核下将滚动条隐藏，Firefox下采用容器覆盖的机制 */
.happy-scroll-container::-webkit-scrollbar  {
display: none;
}
.happy-scroll-container {
overflow: auto;
}

.happy-scroll-container .happy-scroll-content{
/* 解决横向滚动差 20px 的问题 */
display: inline-block;
}


.happy-scroll{
  width: auto!important;
  .happy-scroll-container{
    width: auto!important;
    height: 100%!important;
    .happy-scroll-content{
      width: 100%!important;
      min-height: 100%!important;
      border-right: 0!important;
      border-bottom: 0!important;
    }
  }
}













































.panel-box{
  padding: 0 20px;
  min-height: 80px;
  .title{
    font-size: 18px;
    color: #131926;
    font-weight: bold;
    padding-bottom: 10px;
    border-bottom: 1px solid #5C5D64;
    margin-bottom: 20px;
  }
  .content{
    height: calc(100% - 36px);
  }
  .info-list{
    li{
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-size: 14px;
      color: #131926;
      min-height: 20px;
      + li{
        margin-top: 10px;
      }
    }
  }
}
