
*{
  margin:0;
  padding:0;
}
html,body{
  width:100%;
  height:100%;
}
ul{list-style:none}
a{text-decoration: none;}
img{vertical-align:top;border:none}

.clearf:after,
.clearf:before{
    content:"";
    display:block;
    clear:both;
}

.happy-scroll {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}

.happy-scroll-strip--vertical {
  position: absolute;
  right: 0px;
  top:0;
  height: 100%;
}
.happy-scroll-strip--vertical .happy-scroll-bar {
  background-color: rgba(51,51,51,0.2);
  border-radius: 7px;
  width: 6px;
  height: 0;
  -webkit-transition: width 0.4s ease, height 0.6s ease;
  transition: width 0.4s ease, height 0.6s ease;
}
.happy-scroll-strip--horizontal {
  position: absolute;
  bottom: 0px;
  left:0;
  width: 100%;
}
.happy-scroll-strip--horizontal .happy-scroll-bar {
  background-color: rgba(51,51,51,0.2);
  border-radius: 7px;
  height: 6px;
  width: 0;
  -webkit-transition: height 0.4s ease, width 0.6s ease;
  transition: height 0.4s ease, width 0.6s ease;
}
/* 在webkit内核下将滚动条隐藏，Firefox下采用容器覆盖的机制 */
.happy-scroll-container::-webkit-scrollbar  {
display: none;
}
.happy-scroll-container {
overflow: auto;
}

.happy-scroll-container .happy-scroll-content{
/* 解决横向滚动差 20px 的问题 */
display: inline-block;
}


.happy-scroll{
  width: auto!important;
  .happy-scroll-container{
    width: auto!important;
    height: 100%!important;
    .happy-scroll-content{
      width: 100%!important;
      min-height: 100%!important;
      border-right: 0!important;
      border-bottom: 0!important;
    }
  }
}

































































































































































#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width:100%;
  height:100%;
  background: #E6E9EC;
  .map-content{
    height: calc(100% - 70px);
    width: 100%;
    position: relative;
  }
  .bottom-info{
    position: fixed;
    bottom: 20px;
    font-size: 16px;
    color: #8C939A;
    text-align: center;
    width: 100%;
  }
  .panel{
    width: 340px;
    height: 100%;
    max-height: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
    background-image: linear-gradient(270deg, rgba(230,233,236,0.70) 0%, #E6E9EC 100%);
    display: flex;
    flex-flow: column nowrap;
    box-sizing: border-box;
    padding-bottom: 90px;
    flex: 1;

    .panel-box{
      flex: auto;
      overflow: hidden;
    }
    .flex-grow{
      flex-grow: 1;
    }
    &.left-panel{
      left: 0;
    }
    &.right-panel{
      right: 0;
    }
    .allChart{
      height: 120px;
      width: 100%;
    }
  }
}
@media screen and (max-width: 800px) {
  #app {
    .panel{
      display: none;
    }
  }
}
